body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* fonts */

@font-face {
  font-family: Argesta;
  src: url(/portfolio-site/static/media/argestadisplay-regular-webfont.6e7c0fbe.ttf);
}

@font-face {
  font-family: North;
  src: url("/portfolio-site/static/media/North Carossela.36914708.otf");
}

@font-face {
  font-family: Sek;
  src: url(/portfolio-site/static/media/Sekogure.18ce37a1.ttf);
}

@font-face {
  font-family: Made;
  src: url(/portfolio-site/static/media/MADEOkineSansPERSONALUSE-Medium.e3771b16.otf);
}

@font-face {
  font-family: Stretch;
  src: url(/portfolio-site/static/media/StretchPro.61072ed9.otf);
}

@font-face {
  font-family: Cool;
  src: url("/portfolio-site/static/media/coolvetica rg.651b51cc.otf");
}

@font-face {
  font-family: Avi;
  src: url(/portfolio-site/static/media/Avigea.7f5142ad.ttf);
}

/* general */

body {
